import logo from './logo.svg';
import TimePicker from './TimePicker'
import DatePicker from './DatePicker'
import './DateTimePicker.css';
import { useState } from 'react';




function DateTimePicker() {


    const setBackgroundIniti = {
        margin: '0 auto',
        maxWidth: '300px',
        marginTop: '150px',
        border: '1px solid rgba(0, 0, 0, 0.3)',
        padding: '30px 20px',
        borderRadius: '5px'
    }
    const [showDate, setShowDate] = useState(false)
    const [showTime, setShowTime] = useState(false)
    const [style, setStyle] = useState({})
    const [background, setBackground] = useState(setBackgroundIniti)


    const handleClickDatePicker = () => {
        setShowDate(!showDate)
        setStyle({ display: 'none' })
        setBackground(
            {
                backgroundColor: '#0d1725',
                position: 'absolute',
                width: '100%',
                height: '100%'
            }
        )
    }

    const handleClickTimePicker = () => {
        setShowTime(!showTime)
        setBackground(
            {
                backgroundColor: '#0d1725',
                position: 'absolute',
                width: '100%',
                height: '100%'
            }
        )
        setStyle({ display: 'none' })
    }

    const [messageDate, setMessageDate] = useState(30)
    const callbackDate = (a) => {
        setMessageDate(a)
    }
    const [messageMinute, setMessageMinute] = useState(45)
    const callackMinute = (a) => {
        setMessageMinute(a)
    }
    const [mesageMonth, setMesageMonth] = useState(1)
    const [messageYear, setMesageYear] = useState(2022)
    const callbackYear = (a) => {
        setMesageYear(a)
    }
    const callbackMonth = (a) => {
        setMesageMonth(a)
    }

    const [mesege, setMesege] = useState(9)
    // const [mesegeDay, setMesegeDay] = useState(0)
    const callback = (a) => {
        setMesege(a)
    }
    const valuetime = mesege + ':' + messageMinute

    const valueDay = messageDate + '/' + mesageMonth + '/' + messageYear

    const handleClickpicker = (a, b, c) => {
        setShowTime(false)
        setStyle({ display: 'block' })
        setBackground(setBackgroundIniti)
    }

    const handleOnclickDate = () => {
        setShowDate(false)
        setStyle({ display: 'block' })
        setBackground(setBackgroundIniti)

    }


    return (

        <div style={background}>
            <form style={style}>
                <label style={{ display: 'block', marginBottom: '30px', fontSize: '18px' }}>
                    Date
                    <input style={{
                        marginLeft: '10px', padding: '8px', fontSize: '18px', borderRadius: '5px',
                        border: '1px solid rgba(0, 0, 0, 0.3)',
                        maxWidth: '200px'
                    }} value={valueDay} onClick={handleClickDatePicker} />
                </label>

                <label style={{ display: 'block', fontSize: '18px' }}>
                    Time
                    <input style={{
                        marginLeft: '10px', padding: '8px', fontSize: '18px', borderRadius: '5px',
                        border: '1px solid rgba(0, 0, 0, 0.3)',
                        maxWidth: '200px'
                    }} value={valuetime} onClick={handleClickTimePicker} />
                </label>
            </form>
            {showDate && <><DatePicker onClickDate={handleOnclickDate}
                paracallYear={callbackYear}
                paracallMonth={callbackMonth} paracallDate={callbackDate} /></>}
            {showTime && <><TimePicker pracallClock={handleClickpicker} paracallMinute={callackMinute} paracall={callback} />
            </>
            }

        </div>
    );
}

export default DateTimePicker;
