import { act } from '@testing-library/react';
import React from 'react';
import TimeMinutePicker from './TimeMinutePicker';
import { useState } from 'react/cjs/react.development';
import './TimePicker.css';



const TimePicker = (props) => {
    const timeHourOut = [...Array(13).keys()]
    timeHourOut.shift()
    const timeHourIn = [...Array(24).keys()].filter(value => value > 12).concat(0)
    const timeMinute = [...Array(60).keys()].concat(0)
    timeMinute.shift()
    let value = 0
    let valueMinute = 0
    const degMinute = timeMinute.map(() => {
        return (
            valueMinute = 360 / (timeMinute.length) + valueMinute
        )
    })
    const deg = timeHourOut.map(() => {
        return (
            value = 360 / (timeHourOut.length) + value
        )
    })
    const handleTimeHourIn = (item, index) => {
        let b = deg[index].toString()

        const a = {
            transform: 'rotate(' + b + 'deg)'
        }
        const styleInTime = {
            transform: 'rotate(' + '-' + b + 'deg)',
            display: 'inline-block',
            width: '35px',
            height: '35px',
            lineHeight: '35px',
            marginLeft: '-17px',
            color: '#7f7e7f'
        }
        const handleTimeIn = () => {
            setValueHour(item)
            setisShowTimeMinute(!isShowTimeMiinute)
        }
        const [hoverNumberIntime, setHoverNumberInTime] = useState(styleInTime)
        const handleOnmouseMoveInTime = () => {
            setHoverNumberInTime({ ...hoverNumberIntime, backgroundColor: '#da4335', borderRadius: '50%', color: '#fff' })
            setHoverTreeInTime(treeInTime)
            setHoverdottreeintime(dotTreetime)
        }
        const handleOnmouseLeaveIntime = () => {
            setHoverNumberInTime(styleInTime)
            setHoverTreeInTime({})
            setHoverdottreeintime({})
        }
        const treeInTime = {
            backgroundColor: '#e24435',
            height: '55px',
            width: '2px',
            display: 'inline-block',
            transform: 'translateX(0px)'
        }
        const dotTreetime = {
            backgroundColor: '#e24435',
            width: '5px',
            height: '5px',
            borderRadius: '50%',
            display: 'inline-block',
            position: 'absolute',
            top: '55px',
            left: '-1px'
        }
        const [hoverTreeInTime, setHoverTreeInTime] = useState({})
        const [hoverdottreeIntime, setHoverdottreeintime] = useState({})
        return (
            <div style={a} className='timeHourIn'>
                <span
                    onMouseLeave={handleOnmouseLeaveIntime}
                    onMouseMove={handleOnmouseMoveInTime}
                    onClick={handleTimeIn}
                    style={hoverNumberIntime}>{item}
                </span>
                <span style={hoverTreeInTime}><span style={hoverdottreeIntime}></span></span>
            </div>
        )
    }

    const handleTimeHourOut = (item, index) => {
        let b = deg[index].toString()

        const a = {
            transform: 'rotate(' + b + 'deg)'
        }
        const c = {
            transform: 'rotate(' + '-' + b + 'deg)',
            display: 'inline-block',
            marginLeft: '-15px',
            width: '35px',
            height: '35px',
            lineHeight: '35px',
            color: '#e3e3e3'
        }
        const treeTime = {
            backgroundColor: '#e24435',
            height: '95px',
            width: '2px',
            display: 'inline-block',
            transform: 'translateX(0px)'
        }
        const handleClickTimeHour = () => {
            setValueHour(item)
            setisShowTimeMinute(!isShowTimeMiinute)
        }
        const dotTreetime = {
            backgroundColor: '#df4335',
            width: '5px',
            height: '5px',
            borderRadius: '50%',
            display: 'inline-block',
            position: 'absolute',
            top: '91px',
            left: '-1px'
        }
        const [hoverNumberTime, setHoverNumberTime] = useState(c)
        const [hoverTreeTime, setHoverTreeTime] = useState({})
        const [hoverdottreetime, setHoverdottreetime] = useState({})
        const handleNumberTime = () => {
            setHoverNumberTime({ ...hoverNumberTime, backgroundColor: '#da4335', borderRadius: '50%' })
            setHoverTreeTime(treeTime)
            setHoverdottreetime(dotTreetime)
        }
        const handleNumberTimeLeave = () => {
            setHoverNumberTime(c)
            setHoverTreeTime({})
            setHoverdottreetime({})
        }

        return (
            <>
                <div
                    style={a}
                    className='timeHourOut'>
                    <span
                        onMouseLeave={handleNumberTimeLeave}
                        onMouseMove={handleNumberTime}
                        className='numberTime'
                        onClick={handleClickTimeHour}
                        style={hoverNumberTime}>{item}
                    </span>
                    <span style={hoverTreeTime}><span style={hoverdottreetime}></span></span>

                </div>
            </>
        )
    }
    const [valueHour, setValueHour] = useState('09')

    const handleButtonOk = () => {
        props.paracall(valueHour)
        props.paracallMinute(getcallMinute)
    }
    const handleButtonCancel = () => {
        props.pracallClock(false, { display: 'block' }, {})
    }
    const [getcallMinute, setgetcallMinute] = useState(45)
    const handleCallTimeMinute = (a) => {
        setgetcallMinute(a)
    }
    const [isShowTimeMiinute, setisShowTimeMinute] = useState(true)
    const handleCallshowTime = (a) => {
        setisShowTimeMinute(a)
    }
    return (
        <>
            <div className='wrapper-clock'>
                <div className='screen'>
                    <span style={{ color: '#fff' }}>{valueHour}</span><span style={{ color: '#c1c1c1' }}>:{getcallMinute}</span>
                </div>
                {timeHourOut.map(handleTimeHourOut)}
                {timeHourIn.map(handleTimeHourIn)}
                {!isShowTimeMiinute && <TimeMinutePicker callshowTime={handleCallshowTime} callTimeMinute={handleCallTimeMinute} />}
                <button className='buttonCancel' onClick={handleButtonCancel}>CANCEL</button>
                <button className='buttonOk' onClick={handleButtonOk}>OK</button>

            </div>

        </>
    )
}

export default TimePicker