import { act } from '@testing-library/react';
import React from 'react';
import { useState } from 'react/cjs/react.development';
import './TimeMinutePicker.css';



const TimeMinutePicker = (props) => {

    const timeMinute = [...Array(60).keys()].concat(0)
    timeMinute.shift()
    let value = 0
    let valueMinute = 0
    const degMinute = timeMinute.map(() => {
        return (
            valueMinute = 360 / (timeMinute.length) + valueMinute
        )

    })

    const handleTimeMinute = (item, index) => {
        let b = degMinute[index].toString()

        const wrapperStyleMinute = {
            transform: 'rotate(' + b + 'deg)',
            position: 'absolute',
            left: 'calc(50% - 3px)',
            top: 'calc(30% - 40px)',
            width: '0',
            height: '320px',
            display: 'inline-block',
            textAlign: 'center',

        }
        const c = {
            transform: 'rotate(' + '-' + b + 'deg)',
            display: 'inline-block',
            width: '20px',
            height: '20px',
            lineHeight: '20px',
            marginLeft: '-16px',
            fontSize: '12px',
            color: '#e3e3e3'
        }
        const treeTimeMinute = {
            backgroundColor: '#e24435',
            height: '135px',
            width: '2px',
            display: 'inline-block',
            transform: 'translateX(-6px)'
        }
        const dotTreetimeMinute = {
            backgroundColor: '#da4335',
            width: '5px',
            height: '5px',
            borderRadius: '50%',
            display: 'inline-block',
            position: 'absolute',
            top: '132px',
            left: '-1px'
        }
        const [styleWrapMinute, setWrapStyleMinute] = useState(wrapperStyleMinute)
        const [handleshowTime, setHandleshowtime] = useState(true)
        const handleClickTimeMinute = () => {
            props.callTimeMinute(item)
            setHandleshowtime(!handleshowTime)
            props.callshowTime(handleshowTime)
        }

        const [styleMinute, setStyleMinute] = useState(c)
        const handleMouseMove = () => {
            setStyleMinute({
                ...styleMinute,
                backgroundColor: '#da4335',
                borderRadius: '50%'
            })
            setWrapStyleMinute({
                ...wrapperStyleMinute, display: 'block',
                backgroundColor: '#da4335',
            })
            setHoverTreeTimeMinute(treeTimeMinute)
            setHoverdottreetimeMinute(dotTreetimeMinute)
        }

        const handleMouseLeave = () => {
            setStyleMinute(c)
            setWrapStyleMinute(wrapperStyleMinute)
            setHoverTreeTimeMinute({})
            setHoverdottreetimeMinute({})
        }
        const [hoverTreeTimeMinute, setHoverTreeTimeMinute] = useState({})
        const [hoverdottreetimeMinute, setHoverdottreetimeMinute] = useState({})
        return (
            <div className='style-wrapper-minute' style={styleWrapMinute}>
                <span
                    onMouseLeave={handleMouseLeave}
                    onMouseMove={handleMouseMove}
                    className='styleTimeMinute'
                    onClick={handleClickTimeMinute}
                    style={styleMinute}>{(item % 5 !== 0) ? '.' : item}
                </span>
                <span style={hoverTreeTimeMinute}><span style={hoverdottreetimeMinute}></span></span>
            </div>
        )
    }

    return (
        <>
            {timeMinute.map(handleTimeMinute)}

        </>
    )
}

export default TimeMinutePicker