import React, { useState } from 'react';
import './DatePicker.css';
import TableCreatYear from './TableYear'
import Left from './left.png'
import Right from './right.png'
import Down from './down.png'
import Up from './up.png'

const DatePicker = (props) => {

    const [getMonth, setGetMonth] = useState('September')
    const day = ['S', 'M', 'T', 'W', 'T', 'F', 'S']
    const monthArray = ['January', 'February',
        'March', 'April', 'May', 'June', 'July', 'August',
        'September', 'October', 'November', 'December']
    const [numberMonth, setnumberMonth] = useState(9)
    const handleBeforeMonth = () => {
        setnumberMonth(numberMonth - 1)
        const a = monthArray.map((item, index) => (index) == (numberMonth - 2) ? item : null).filter(item => item != null).toString()

        setGetMonth(a)
        if (numberMonth < 3) {
            setnumberMonth(13)
        }
    }


    const handleAfterMonth = () => {

        setnumberMonth(numberMonth + 1)
        const a = monthArray.map((item, index) => index == (numberMonth) ? item : null).filter((item) => item != null).toString()
        setGetMonth(a)

        if (numberMonth > 10) {
            setnumberMonth(0)
        }
        console.log(numberMonth)
    }


    const getDay = (day, month, year) => {
        if (month == 1) {
            month = 13;
            year--;
        }
        if (month == 2) {
            month = 14;
            year--;
        }
        let q = day;
        let m = month;
        let k = year % 100;
        let j = parseInt(year / 100, 10);
        let h = q + parseInt(13 * (m + 1) / 5, 10) + k + parseInt(k / 4, 10) + parseInt(j / 4, 10) + 5 * j;
        h = h % 7;
        return h
    }


    const daysIn = (year, month) => {
        return new Date(year, month, 0).getDate();
    }
    const calendar = (month, year) => {
        var startIndex = getDay(1, month, year);
        var endIndex = daysIn(year, month);
        var result = Array.apply(0, Array(42)).map(function (i) { return 0; });
        for (let i = startIndex; i <= endIndex; i++) {
            if (startIndex == 0) {
                result[i + 6] = i + 1
            } else {
                result[i - 1] = (i - startIndex) + 1;
            }
        }
        return result;
    }

    const [mesageYear, setMesageYear] = useState(2022)
    const callBackYear = (a) => {
        setMesageYear(a)
    }
    console.log(mesageYear)


    const numerDay = calendar(numberMonth, mesageYear)

    const handleDayNumber = (item, index) => {

        const handleNumberDay = () => {
            props.paracallDate(item)
            props.onClickDate()
            props.paracallMonth(numberMonth)
            props.paracallYear(mesageYear)
        }
        const styleNumberday = {
            display: 'inline-block',
            width: '50px',
            textAlign: 'center',
            height: '50px',
            borderRadius: '50%',
            backgroundColor: '#121211',
            color: '#ebebec',
            lineHeight: '50px',
            margin: '5px'

        }
        const [hoverNumberday, setHoverNumberDay] = useState(styleNumberday)
        const handleOnmouseMoveNumberDay = () => {
            setHoverNumberDay({ ...hoverNumberday, backgroundColor: '#5ea3ee', color: '#101d2b' })
        }
        const handleOnmouseLeaveNumberDay = () => {
            setHoverNumberDay(styleNumberday)
        }
        if (item == 0) {
            return (
                <>
                    <a
                        className='numberDaywithzero'
                        style={{
                            visibility: 'hidden', display: 'inline-block', width: '50px',
                            height: '50px', borderRadius: '50%',
                            lineHeight: '50px', margin: '5px'
                        }}
                        key={index}>{item}
                    </a>
                </>
            )
        } else {
            return (
                <>
                    <a
                        className='numberDay'
                        onMouseMove={handleOnmouseMoveNumberDay}
                        onMouseLeave={handleOnmouseLeaveNumberDay}
                        onClick={handleNumberDay}
                        style={hoverNumberday}
                        key={index}>{item}
                    </a>
                </>
            )
        }

    }

    const [styleYear, setStyleYear] = useState(true)
    const handleYear = () => {
        setStyleYear(!styleYear)
    }

    return (
        <>
            <div className='wrapper-date-picker'>
                <div className='wrapper-header'>
                    <div style={{ display: 'inline-block' }} onClick={handleYear}>
                        <span className='monthstyle'>{getMonth}</span>
                        <span className='yearstyle'>{mesageYear}</span>
                        <span>{styleYear ? <img src={Down} /> : <img src={Up} />}</span>
                    </div>
                    <div style={{ display: 'inline-block', float: 'right' }}>
                        <span onClick={handleBeforeMonth}><img src={Left} /></span>
                        <span style={{ marginLeft: '60px', paddingTop: '8px' }} onClick={handleAfterMonth}><img src={Right} /></span>
                    </div>
                </div>

                {!styleYear && <TableCreatYear call={callBackYear} />}
                <div
                    className='week-header'
                >
                    {day.map((item, index) => <a className='week-number'
                        key={index}>{item}
                    </a>)}
                </div>
                <div className='wrapper-numberday' >{numerDay.map(handleDayNumber)}</div>
            </div>

        </>
    )
}

export default DatePicker