import React from 'react';
import { useState } from 'react/cjs/react.development';
import './TableYear.css';


const TableCreatYear = (props) => {
    const arrayYear = [...Array(2032).keys()].filter((value) => value > 1999)
    const stylegetYear = {
        width: '480px',
        backgroundColor: '#302f2f',
        position: 'absolute',
        height: '450px',
        zindex: '1',
        marginTop: '30px',
        overflowY: 'scroll'
    }


    const handleArrayYear = (item, index) => {
        const [getvaluetime, setGetvaleTime] = useState(item)

        const handleTableYear = () => {

            setGetvaleTime(item)
            setGetvaleTime(item)
            props.call(getvaluetime)
            console.log(getvaluetime)

        }
        return (
            <a
                onClick={handleTableYear}
                style={
                    {
                        display: 'inline-block',
                        width: '110px',
                        textAlign: 'center',
                        padding: '20px 0',
                        color: '#fff'
                    }
                }
            >
                {item}
            </a>
        )
    }
    return (
        <div className='style-wrapper' style={stylegetYear}>{arrayYear.map(handleArrayYear)}</div>
    )
}

export default TableCreatYear